.feedback{
    background: #f8fcfe;
}

.feedback_text{
    text-align: center;
    font-family: 'Montserrat', sans-serif;
    font-size: 36px;
    font-weight: 400;
    margin-top: 67px;
}

.feedback-box{
    background: #ffffff;
    width: 1192px;
    height: 616px;
    opacity: 0.8;
    border: 4px solid #cbdbda;
    -webkit-box-shadow: 0 0 10px rgba(0,0,0,0.5);
            box-shadow: 0 0 10px rgba(0,0,0,0.5);
}
.feedback-item{
    margin-top: 75px;
    margin-bottom: 120px;
}
.feadblack-input{
    width: 560px;
    height: 75px;
    margin-bottom: 6px;
    font-family: 'Montserrat', sans-serif;
    font-size: 24px;
    font-weight: 300;
    color: #90aebd;
    padding-left: 20px;
    border: 2px solid #d6e3e3;
}
.feedback-textarea{
    width: 560px;
    height: 120px;
    font-family: 'Montserrat', sans-serif;
    font-size: 24px;
    font-weight: 300;
    color: #90aebd;
    padding-left: 20px;
    border: 2px solid #d6e3e3;
}

.feedback-btn{
    margin-top: 27px;
}
.feedback-btn h2{
    font-family: 'Montserrat', sans-serif;
    font-size: 36px;
    font-weight: 400;
    color: #4e4e4e;
}

.feedback-button{
    margin-left: 0;
}

.feedback-btn button{
    margin-top: 0;
}
.feedback-button{
    background: #18cfab;
    border-radius: 5px;
    border: 2px solid #18cfab;
    color: #ffffff;
    font-family: 'Montserrat', sans-serif;
    font-size: 14px;
    font-weight: 700;
    text-transform: uppercase;
    
}
.feedback-button span{
    letter-spacing: 10px;
}




